<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import { KinesisContainer, KinesisElement } from 'vue-kinesis'

export default {
  name: "Home",
  data: function(){
    const _vm = this

    return {
      options: {
        licenseKey : 'YOUR_KEY_HERE',
        afterLoad: this.afterLoad,
        scrollOverflow: false,
        scrollBar: false,
        menu: '#myMenu',
        navigation: true,
        navigationPosition: 'right',
        navigationTooltips: ['', '', '', ''],
        anchors: ['page1', 'page2', 'page3', 'page4'],
        sectionsColor: ['#fff', '#fff', '#eee', '#fff'],
        scrollingSpeed: 1000,
        pagination: {
          el: '#myMenu',
          clickable: true
        },
        navigationIndex: (1),
        easing: [1, 0, 0, 1],
      },
      swiperOption: {
        slidesPerView: 'auto',
        spaceBetween: 0, // swiper-slide 사이의 간격 지정
        slidesOffsetBefore: 0, // slidesOffsetBefore는 첫번째 슬라이드의 시작점에 대한 변경할 때 사용
        slidesOffsetAfter: 0, // slidesOffsetAfter는 마지막 슬라이드 시작점 + 마지막 슬라이드 너비에 해당하는 위치의 변경이 필요할 때 사용
        freeMode: false, // freeMode를 사용시 스크롤하는 느낌으로 구현 가능
        centerInsufficientSlides: true, // 컨텐츠의 수량에 따라 중앙정렬 여부를 결정함
        click: function () {
          _vm.slideMoveTo()
          _vm.activeTab()
        },
        tap: function () {
          _vm.slideMoveTo()
          _vm.activeTab()
        },
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
      }
    }
  },
  components: {
    swiper,
    swiperSlide,
    KinesisContainer,
    KinesisElement
  },
  methods: {
    afterLoad () {
      console.log('After load')
    },
    slideMoveTo: function (swiper = this.swiper) {
      // 그라데이션 영역이나 슬라이드 사이의 여백을 터치할 경우 이벤트가 발생하지 않도록 처리
      // 슬라이드 이동의 초기값은 initialSlide에 영향을 받기 때문에 activeTab에 넣었던 hasOwnProperty 조건이 불필요
      if (!swiper.clickedSlide) return

      const activeIndex = swiper.clickedIndex
      swiper.slideTo(activeIndex)
    },
    Private: function () {
      window.alert("비공개사이트입니다.")
    },
    rirosoft: function () {   
        window.open("https://rirosoft.com/RiroInfo#1", "_blank");    
    },
    riroschool: function () {   
        window.open("https://rirosoft.com", "_blank");    
    },
    daiso: function () {
      window.open("https://shop.daiso.co.kr/mobile_new/main/index.do", "_black");
    },
    daeguro: function () {
      window.open("https://daeguro.co.kr/", "_black");
    },
    yellowballoon: function () {
      window.open("https://www.ybtour.co.kr/", "_black");
    },
    yb: function () {
      window.open("http://company.ybtour.co.kr/main.do", "_black");
    }
  }
}
</script>

